import { useCallback, useState, useEffect } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Contract } from 'web3-eth-contract'
import { useDispatch } from 'react-redux'
import { updateUserAllowance } from 'state/actions'
import { getAllowance } from 'utils/erc20'
import { approve } from 'utils/callHelpers'
import BigNumber from 'bignumber.js'
import { fetchFarmUserDataAsync } from '../state/farms'
import { useMasterchef, useSousChef } from './useContract'
import useBlock from './useBlock'

export const useAllowance = (lpContract: Contract, contract: Contract) => {
  const [isApproved, setIsApproved] = useState(false)
  const { account }: { account: string } = useWallet()
  const block = useBlock()

  useEffect(() => {
    if (isApproved) return
    getAllowance(lpContract, contract, account).then((x) => {
      if (!account) return
      const allowance = new BigNumber(x)
      setIsApproved(allowance && allowance.isGreaterThan(0))
    })
  }, [account, contract, lpContract, block, isApproved])

  return isApproved
}

export const useApprove = (lpContract: Contract, amount?: BigNumber) => {
  const dispatch = useDispatch()
  const { account }: { account: string } = useWallet()
  const masterChefContract = useMasterchef()
  const handleApprove = useCallback(async () => {
    try {
      const tx = await approve(lpContract, masterChefContract, account, amount)

      if (!amount)
        dispatch(fetchFarmUserDataAsync(account))

      return tx
    } catch (e) {
      console.error(e)
      return false
    }
  }, [account, dispatch, lpContract, masterChefContract, amount])

  return { onApprove: handleApprove }
}

export const useContractApprove = (lpContract: Contract, contract: Contract) => {
  const { account }: { account: string } = useWallet()
  const handleApprove = useCallback(async () => {
    try {
      return await approve(lpContract, contract, account)
    } catch (e) {
      console.error(e)
      return false
    }
  }, [account, lpContract, contract])

  return { onApprove: handleApprove }
}

// Approve a Pool
export const useSousApprove = (lpContract: Contract, sousId) => {
  const dispatch = useDispatch()
  const { account }: { account: string } = useWallet()
  const sousChefContract = useSousChef(sousId)

  const handleApprove = useCallback(async () => {
    try {
      const tx = await approve(lpContract, sousChefContract, account)
      dispatch(updateUserAllowance(sousId, account))
      return tx
    } catch (e) {
      return false
    }
  }, [account, dispatch, lpContract, sousChefContract, sousId])

  return { onApprove: handleApprove }
}
